
.wishlist .modal-body h3, h3 + p{
    font: 20px var(--font-family-syne)
}

.wishlist .modal-body .pro_img{
    width: 100%;
    height:80%
}
.wishlist .modal-body .link_img{
    bottom: 47px;
    right: 24px;
}

.btn_modal,
.btn-close{
    background-color: #000000ad;
    border: none;
    opacity: 1;
}

.modal-footer, .modal-header{
    background-color: #000000bb;
    color: #000;
   
}

.wishlist .owl-carousel-item{ width: 25%; }

.modal-title{ font: 28px var(--font-family-worksans);  font-weight: 500;}

@media  screen and (max-width: 768px) {
    .wishlist .owl-carousel-item{ width: 100%; }
    .wishlist .modal-body .link_img{
        bottom: 77px;
        right: 0px;
    }
}